.user__photo {
    width: 80px;
    height: 80px;
    border: 1px solid black;
    border-radius: 50%;
}

.user__action {
    border: 1px solid rgba(143, 143, 143, 0.8);
    font-family: Monserat, sans-serif;
    font-size: 15px;
    padding: 7px;
    background-color: #25ddde;
    margin: 5px auto;
    align-self: center;
}

.user__action:hover{
    background-color: rgba(66,83,218,0.32);
    cursor: pointer;
    transition: background-color .1s;
}

.user__field {
    background-color: white;
    padding: 15px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.user__finfo {

}

.user__title {
    font-family: Monserat, sans-serif;
    font-size: 22px;
    color: black;
}

.user__status {
    margin-top: 20px;
    font-family: Monserat, sans-serif;
    font-size: 18px;
    color: black;
}

.user__region {
    font-family: Monserat, sans-serif;
    font-size: 18px;
    color: black;
}

.users {
    background-color: white;
    width: 100vw;
    padding: 15px;
}

.user__block{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.users__block2{
    padding: 0 15px;
}

.selectedpage{
    font-weight: bold;
}

.pagination__item:hover {
    cursor: pointer;
}
